html{
  height: 100vh;
}

button {
  padding: 0;
  border: none;
}

button:focus {
  outline: none;
}

.icon {
  position: relative;
  width: var(--size, 30px);
  height: var(--size, 30px);
  transition: .15s;
}
.icon svg {
  width: 100%;
  height: 100%;
}

.toggle-icons svg {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}
.toggle-icons svg:nth-of-type(1) {
  opacity: 1;
}
.toggle-icons svg:nth-of-type(2) {
  opacity: 0;
}

.toggle-icons.toggled svg:nth-of-type(1) {
  opacity: 0;
}
.toggle-icons.toggled svg:nth-of-type(2) {
  opacity: 1;
}

.light-theme {
