.react-toggle-thumb {
  border: 1px solid #4D4D4D;
  border-radius: 50%;
  background-color: #FAFAFA;
}

.react-toggle--checked .react-toggle-thumb {
  left: 27px;
  border-color: #393836;
}

.react-toggle--focus .react-toggle-thumb {
  -webkit-box-shadow: 0px 0px 3px 2px #e6e6e6;
  -moz-box-shadow: 0px 0px 3px 2px #e6e6e6;
  box-shadow: 0px 0px 2px 3px #e6e6e6;
}

.react-toggle:active:not(.react-toggle--disabled) .react-toggle-thumb {
  -webkit-box-shadow: 0px 0px 5px 5px #e6e6e6;
  -moz-box-shadow: 0px 0px 5px 5px #e6e6e6;
  box-shadow: 0px 0px 5px 5px #e6e6e6;
}

.react-toggle-track {
  width: 50px;
  height: 24px;
  padding: 0;
  border-radius: 30px;
  background-color: #393836;
  -webkit-transition: all 0.2s ease;
  -moz-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.react-toggle:hover:not(.react-toggle--disabled) .react-toggle-track {
  background-color: #393836;
}

.react-toggle--checked .react-toggle-track {
  background-color: #c2403c;
}

.react-toggle--checked:hover:not(.react-toggle--disabled) .react-toggle-track {
  background-color: #c2403c;
}

